const AppSidebar = () => {
  return ( 
    <Sidebar className="w-16 md:w-20 lg:w-64 p-2 border-none bg-gray-100">
  <SidebarContent className="bg-gray-100">
    <SidebarGroup className="bg-gray-100 mt-10">
      <SidebarGroupContent>
        <SidebarMenu>
          {items.map((item) => (
            <SidebarMenuItem key={item.title || item.url}>
              <SidebarMenuButton asChild>
                <a
                  href={item.url}
                  className="relative group p-2 hover:bg-white rounded-md flex items-center justify-center"
                >
                  <item.icon className="w-5 h-5 md:w-6 md:h-6" />
                </a>
              </SidebarMenuButton>
            </SidebarMenuItem>
          ))}
        </SidebarMenu>
      </SidebarGroupContent>
    </SidebarGroup>
  </SidebarContent>
</Sidebar>

   );
}
 
export default AppSidebar;