
import { MessageCircleCode } from "lucide-react";
import { AlertCircle } from "lucide-react";
import { BookLock } from "lucide-react";
const item = [
    {
        title: 'Reports',
        icon: BookLock,
    },
    {
        title: 'Admin',
        icon: BookLock,
    },
    {
        title: 'Personnel',
        icon: BookLock,
    },
    {
        title: 'Safety',
        icon: BookLock,
    },
    {
        title: 'Ecology',
        icon: BookLock,
    }
]
const NavBar = () => {
    return (
        <div className="bg-red-400 w-[100vw] h-20">
            <nav className="flex items-center justify-between">
                <h1 className="font-semibold">Reconville</h1>
                {
                     item.map((items) =>(
                        <button className="flex items-center bg-white h-10 w-22 rounded-md">
                            <items.icon/>
                            <p className="text-sm">{items.title}</p>
                        </button>
                     ))   
                }
                <div className="flex items-center gap-4">
                    <div><AlertCircle size={20}/></div>
                    <div><MessageCircleCode size={20}/></div>
                </div>
             </nav>
        </div>
    );
}

export default NavBar;